import { useTaskContext } from "../../context/taskContext";

import { motion as m } from "framer-motion";
import { listSlideLeft, buttonHoverTap } from "../../animations/animations";
import { useGlobalContext } from "../../context/appContext";

export default function TodoItem({ todo }) {
  const { setEditMode } = useGlobalContext();

  const { finishTodo, deleteTodo, getSingleTodo } = useTaskContext();
  return (
    <m.div
      variants={listSlideLeft}
      initial="hidden"
      animate="show"
      exit="exit"
      whileHover="hover"
      key={todo._id}
      className="todo-item"
    >
      <div className="task-progress__container">
        <input
          className="todo-progress"
          type="checkbox"
          name="status"
          id="todoSingleStatus"
          checked={todo.finished}
          onChange={() => {
            finishTodo(todo._id);
          }}
        />

        <h4 className={todo.favorite ? "todo-task fave" : "todo-task"}>
          {todo.task}
        </h4>
      </div>

      <div className="edit-delete__container">
        <m.button
          variants={buttonHoverTap}
          whileHover="hover"
          whileTap="tap"
          className="edit-button"
          id="todoSingleDelete"
          onClick={() => {
            setEditMode(true);
            getSingleTodo(todo._id);
          }}
        >
          EDIT
        </m.button>
        <m.button
          variants={buttonHoverTap}
          whileHover="hover"
          whileTap="tap"
          className="delete-button"
          id="todoSingleDelete"
          onClick={() => {
            deleteTodo(todo._id);
          }}
        >
          X
        </m.button>
      </div>
    </m.div>
  );
}
