import { useGlobalContext } from "../../context/appContext";
import { useTaskContext } from "../../context/taskContext";

import { motion as m } from "framer-motion";
import {
  pageOpacity,
  headerTitle,
  buttonHoverTapSmall,
  buttonHoverTap,
} from "../../animations/animations";

export default function TodoEdit() {
  const { setEditMode } = useGlobalContext();
  const { singleTodo, setSingleTodo, updateTodo } = useTaskContext();
  return (
    <m.div
      variants={pageOpacity}
      initial="hidden"
      animate="show"
      exit="exit"
      className="edit-form"
    >
      <m.form
        variants={headerTitle}
        exit={{ opacity: 0 }}
        className="edit-form__container"
        onSubmit={(e) => {
          e.preventDefault();
          updateTodo(singleTodo._id);
          setEditMode(false);
        }}
      >
        <m.div className="edit__top">
          <div className="edit__title">EDIT TODO</div>
        </m.div>

        <label htmlFor="todoEditTask" className="edit__task-label">
          <span>Task</span>
          <input
            className="form__input"
            id="todoEditTask"
            value={singleTodo.task}
            onChange={(e) => {
              setSingleTodo((prev) => {
                return { ...prev, task: e.target.value };
              });
            }}
          />
        </label>
        <m.div
          variants={buttonHoverTap}
          whileHover="hover"
          whileTap="tap"
          className="favorite-container"
        >
          <label htmlFor="favoriteTodo" className="favorite-label">
            <input
              type="checkbox"
              className="favorite-check"
              id="favoriteTodo"
              checked={singleTodo.favorite}
              onChange={(e) => {
                setSingleTodo((prev) => {
                  return { ...prev, favorite: e.target.checked };
                });
              }}
            />
            <span className="favorite-span">
              {singleTodo?.favorite ? "FAVORITE" : "ADD TO FAVORITES"}
            </span>
          </label>
        </m.div>
        <div className="edit-buttons">
          <m.button
            variants={buttonHoverTapSmall}
            whileHover="hover"
            whileTap="tap"
            className="edit__submit"
            type="submit"
          >
            EDIT
          </m.button>
          <m.button
            variants={buttonHoverTapSmall}
            whileHover="hover"
            whileTap="tap"
            className="edit__exit"
            onClick={(e) => {
              e.preventDefault();
              setEditMode(false);
            }}
          >
            CANCEL
          </m.button>
        </div>
      </m.form>
    </m.div>
  );
}
