import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useGlobalContext } from "../context/appContext";
import { useAuthContext } from "../context/authContext";
import { useTaskContext } from "../context/taskContext";
import EditProfile from "../components/Dashboard/EditProfile";
import defaultImage from "../assets/images/default.jpg";
import { motion as m, AnimatePresence } from "framer-motion";
import {
  nav2,
  pageOpacity,
  dashLogout,
  footer,
  itemSlideLeft,
  v1Logo,
  buttonHoverTap,
  dashModeHover,
  dashModeHighlight,
} from "../animations/animations";

export default function Dashboard() {
  const { getQuote, quote, author, setIsLoading, editMode, setEditMode } =
    useGlobalContext();
  const { logoutUser, setUpdateUser, isUserLoggedIn, user } = useAuthContext();
  const { getAllData, todos, notes, pomodoros } = useTaskContext();
  const navigate = useNavigate();

  useEffect(() => {
    setIsLoading(true);
    if (isUserLoggedIn) {
      getQuote();
      getAllData();
      setEditMode(false);
    } else {
      setEditMode(false);
      navigate("/");
    }
    setIsLoading(false);
  }, []);

  return (
    <m.div
      variants={pageOpacity}
      initial="hidden"
      animate="show"
      exit="exit"
      className="dashboard-page"
    >
      <div className="dashboard__nav">
        <div className="nav-2  ">
          <m.div
            variants={dashLogout}
            initial="hidden"
            animate="show"
            exit="exit"
            whileHover="hover"
            whileTap="tap"
            className="nav-2__close "
            onClick={async (e) => {
              e.preventDefault();
              logoutUser();
              navigate("/");
            }}
          >
            LOG OUT
          </m.div>
        </div>
      </div>
      <m.div variants={nav2} className="dashboard__header">
        <m.div variants={dashLogout} className="header__image-container">
          <img
            className="header__image"
            src={user?.image || defaultImage}
            alt="profile-pic"
          />
          <m.button
            variants={buttonHoverTap}
            whileHover="hover"
            whileTap="tap"
            className="header__image-edit"
            onClick={(e) => {
              e.preventDefault();
              setUpdateUser(user);
              setEditMode(true);
            }}
          >
            EDIT PROFILE
          </m.button>
        </m.div>
        <m.div variants={dashLogout} className="header__text-container">
          <div className="text__welcome">hi, {user?.name || "user"}</div>
          <div className="text__quotes-container">
            <div className="quotes-quote">{quote}</div>
            <div className="quotes-author">-{author}</div>
          </div>
        </m.div>
        <m.div
          variants={v1Logo}
          animate="show"
          whileTap="tap"
          drag
          dragConstraints={{
            top: -10,
            left: -10,
            right: 10,
            bottom: 10,
          }}
          className="header__version-container"
        >
          <div className="version__text">v1</div>
        </m.div>
      </m.div>
      <m.div variants={itemSlideLeft} className="dashboard__body">
        <m.div
          variants={dashModeHover}
          whileHover="hover"
          onClick={() => navigate("/note")}
          className="body__mode-container notes"
        >
          <div className="mode__link notes">
            notes
            <m.div
              variants={dashModeHighlight}
              initial="hidden"
              animate="show"
              className="mode__link-highlight"
            ></m.div>
          </div>
          <div className="mode__total notes"> {`(${notes?.length})`}</div>
        </m.div>

        <m.div
          variants={dashModeHover}
          whileHover="hover"
          onClick={() => navigate("/todo")}
          className="body__mode-container todo"
        >
          <div className="mode__link todo">
            todo
            <m.div
              variants={dashModeHighlight}
              initial="hidden"
              animate="show"
              className="mode__link-highlight"
            ></m.div>
          </div>
          <div className="mode__total todo">{`(${todos?.length})`}</div>
        </m.div>

        <m.div
          variants={dashModeHover}
          whileHover="hover"
          onClick={() => navigate("/pomodoro")}
          className="body__mode-container pomo"
        >
          <div className="mode__link pomo">
            pomo
            <m.div
              variants={dashModeHighlight}
              initial="hidden"
              animate="show"
              className="mode__link-highlight"
            ></m.div>
          </div>
          <div className="mode__total pomo">{`(${pomodoros?.length})`} </div>
        </m.div>
      </m.div>
      <m.div
        variants={footer}
        initial="hidden"
        animate="show"
        exit="exit"
        className="dashboard__footer"
      >
        <m.div variants={dashLogout} className="footer__graph-container">
          <div
            className="graph__item notes"
            style={{ flexGrow: notes?.length }}
          ></div>
          <div
            className="graph__item todo"
            style={{ flexGrow: todos?.length }}
          ></div>
          <div
            className="graph__item pomo"
            style={{ flexGrow: pomodoros?.length }}
          ></div>
        </m.div>
        <m.div variants={dashLogout} className="footer__legend-container">
          <div className="legend__item-container">
            <div className="item__disp-text notes">notes</div>
            <div className="item__color notes"></div>
          </div>
          <div className="legend__item-container">
            <div className="item__disp-text todo">todos</div>
            <div className="item__color todo"></div>
          </div>
          <div className="legend__item-container">
            <div className="item__disp-text pomo">pomo</div>
            <div className="item__color pomo"></div>
          </div>
        </m.div>
      </m.div>
      <AnimatePresence>
        {editMode && <EditProfile></EditProfile>}
      </AnimatePresence>
    </m.div>
  );
}
