import React from "react";

import { useTaskContext } from "../../context/taskContext";
import { useGlobalContext } from "../../context/appContext";

import { useForm } from "react-hook-form";

import { motion as m } from "framer-motion";
import {
  headerTitle,
  pageOpacity,
  buttonHoverTapSmall,
} from "../../animations/animations";

export default function PomodoroEdit() {
  const { setSinglePomodoro, singlePomodoro, updatePomodoro } =
    useTaskContext();
  const { setEditMode } = useGlobalContext();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  return (
    <m.div
      variants={pageOpacity}
      initial="hidden"
      animate="show"
      exit="exit"
      className="edit-form"
    >
      <m.form
        variants={headerTitle}
        exit={{ opacity: 0 }}
        className="edit-form__container"
        onSubmit={handleSubmit((data) => {
          updatePomodoro(singlePomodoro._id, data.title, data.favorite);
          console.log(data);
          setEditMode(false);
        })}
      >
        <div className="edit__top">
          <div className="edit__title">EDIT POMODORO</div>
        </div>

        <label className="edit__task-label">
          <span>Task</span>
          <input
            className={
              errors.title?.message ? "form__input invalid" : "form__input"
            }
            {...register("title", {
              required: "Input title",
              minLength: {
                value: 2,
                message: "Task must have of at least 2 characters",
              },
            })}
            defaultValue={singlePomodoro.title}
          />
        </label>
        <m.div
          variants={buttonHoverTapSmall}
          whileHover="hover"
          whileTap="tap"
          className="favorite-container"
        >
          <label htmlFor="favoritePomo" className="favorite-label">
            <input
              {...register(
                "favorite",

                {
                  onChange: (e) => {
                    setSinglePomodoro((prev) => {
                      return { ...prev, favorite: e.target.checked };
                    });
                  },
                }
              )}
              type="checkbox"
              className="favorite-check"
              id="favoritePomo"
              checked={singlePomodoro.favorite}
            />
            <span className="favorite-span">
              {singlePomodoro?.favorite ? "FAVORITE" : "ADD TO FAVORITES"}
            </span>
          </label>
        </m.div>

        <div className="edit-buttons">
          <m.button
            variants={buttonHoverTapSmall}
            whileHover="hover"
            whileTap="tap"
            className="edit__submit"
            type="submit"
          >
            EDIT
          </m.button>
          <m.button
            variants={buttonHoverTapSmall}
            whileHover="hover"
            whileTap="tap"
            className="edit__exit"
            onClick={(e) => {
              e.preventDefault();
              setEditMode(false);
            }}
          >
            CANCEL
          </m.button>
        </div>
      </m.form>
      {errors.title && (
        <div className="edit__error">
          <div>{errors.title?.message}</div>
        </div>
      )}
    </m.div>
  );
}
