import { useEffect } from "react";
import { useNavigate } from "react-router-dom";

import { useGlobalContext } from "../context/appContext";
import { useTaskContext } from "../context/taskContext";

import NoteFormNew from "../components/Note/NoteFormNew";
import NoteSaveModule from "../components/Note/NoteSaveModule";

import { motion as m } from "framer-motion";
import {
  pageAnimationLeft,
  buttonHoverTapSmall,
} from "../animations/animations";

export default function NotesSingleNew() {
  const navigate = useNavigate();
  const { setIsLoading } = useGlobalContext();
  const {
    singleNote,
    setIsNewNote,
    setIsChanged,
    isChanged,
    setIsClosing,
    isClosing,
  } = useTaskContext();

  useEffect(() => {
    setIsLoading(true);
    setIsChanged(false);
    setIsNewNote(!singleNote);
    return setIsLoading(false);
  }, []);

  return (
    <m.div
      variants={pageAnimationLeft}
      initial="hidden"
      animate="show"
      exit="exit"
      className="note-single-page"
    >
      <div className="nav-2">
        <m.div
          variants={buttonHoverTapSmall}
          whileHover="hover"
          whileTap="tap"
          className="nav-2__close"
          onClick={() => {
            isChanged ? setIsClosing(true) : navigate("/note");
          }}
        >
          CLOSE
        </m.div>
      </div>
      <NoteFormNew></NoteFormNew>
      {isClosing && <NoteSaveModule></NoteSaveModule>}
    </m.div>
  );
}
