import React from "react";
import { useTimer } from "react-timer-hook";
import { useState, useEffect } from "react";
import { CircularProgressbar } from "react-circular-progressbar";
import { useTaskContext } from "../../context/taskContext";

import { motion as m } from "framer-motion";
import { buttonHoverTap } from "../../animations/animations";

export default function PomodoroTimer({ expiryTimestamp }) {
  const {
    focusTimeSeconds,
    restTimeSeconds,
    singlePomodoro,
    startPomodoro,
    isRest,
    setIsRest,
    percent,
    setPercent,
  } = useTaskContext();

  const { seconds, minutes, isRunning, start, pause, resume, restart } =
    useTimer({
      expiryTimestamp,
      autoStart: false,
    });

  useEffect(() => {
    let minutesInSeconds = minutes * 60;
    let totalSeconds = minutesInSeconds + seconds;

    if (!isRest) {
      let percentage = (totalSeconds / focusTimeSeconds) * 100;
      setPercent(percentage);
    } else {
      let percentage = (totalSeconds / restTimeSeconds) * 100;
      setPercent(percentage);
    }
    if (minutes + seconds === 0 && !isRunning) {
      if (!isRest) {
        startPomodoro(singlePomodoro._id);

        const time = new Date();
        time.setSeconds(time.getSeconds() + restTimeSeconds);
        setIsRest(true);
        restart(time);
      }
      if (isRest) {
        const time = new Date();
        time.setSeconds(time.getSeconds() + focusTimeSeconds);
        setIsRest(false);
        restart(time);
      }
    }
  }, [seconds, minutes]);

  const showText = () => {
    if (isRest === null) {
      return <div className="timer-status">{"START"}</div>;
    } else if (isRest === false || true) {
      return <div className="timer-status">{isRest ? "REST" : "FOCUS"}</div>;
    }
  };

  return (
    <div className="pomodoro-timer__container">
      {showText()}

      <div className="container">
        <CircularProgressbar
          value={percent}
          text={`${minutes < 10 ? "0" + minutes : minutes}:${
            seconds < 10 ? "0" + seconds : seconds
          }`}
        />
      </div>

      <div className="timer__buttons">
        <m.button
          variants={buttonHoverTap}
          whileHover="hover"
          whileTap="tap"
          onClick={() => {
            setIsRest(false);
            start();
          }}
          className="timer__button"
        >
          START
        </m.button>
        <m.button
          variants={buttonHoverTap}
          whileHover="hover"
          whileTap="tap"
          onClick={pause}
          className="timer__button"
        >
          PAUSE
        </m.button>
        <m.button
          variants={buttonHoverTap}
          whileHover="hover"
          whileTap="tap"
          onClick={resume}
          className="timer__button"
        >
          RESUME
        </m.button>

        <m.button
          variants={buttonHoverTap}
          whileHover="hover"
          whileTap="tap"
          onClick={() => {
            setIsRest(false);
            const time = new Date();
            time.setSeconds(time.getSeconds() + focusTimeSeconds);
            restart(time);
          }}
          className="timer__button"
        >
          RESTART
        </m.button>
      </div>
    </div>
  );
}
