import React from "react";
import { useNavigate } from "react-router-dom";
import { useTaskContext } from "../../context/taskContext";
import { motion as m } from "framer-motion";
import {
  headerTitle,
  pageOpacity,
  buttonHoverTapSmall,
} from "../../animations/animations";

export default function NoteSaveModule() {
  const { setIsClosing } = useTaskContext();
  const navigate = useNavigate();
  return (
    <m.div
      variants={pageOpacity}
      initial="hidden"
      animate="show"
      exit="exit"
      className="note__module-container"
    >
      <m.div variants={headerTitle} className="note__module-box">
        <div className="note__module-text">Leave unsaved progress?</div>
        <m.button
          variants={buttonHoverTapSmall}
          whileHover="hover"
          whileTap="tap"
          className="module-yes"
          onClick={() => {
            setIsClosing(false);
            navigate("/note");
          }}
        >
          YES
        </m.button>
        <m.button
          variants={buttonHoverTapSmall}
          whileHover="hover"
          whileTap="tap"
          className="module-no"
          onClick={() => {
            setIsClosing(false);
          }}
        >
          NO
        </m.button>
      </m.div>
    </m.div>
  );
}
