import { useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";

import { useGlobalContext } from "../context/appContext";
import { useTaskContext } from "../context/taskContext";
import { useAuthContext } from "../context/authContext";
import NoteFormEdit from "../components/Note/NoteFormEdit";
import NoteSaveModule from "../components/Note/NoteSaveModule";

import { motion as m } from "framer-motion";
import {
  pageAnimationLeft,
  buttonHoverTapSmall,
} from "../animations/animations";

export default function NotesSingle() {
  const { id } = useParams();
  const { setIsLoading, fakeId } = useGlobalContext();
  const { user } = useAuthContext();
  const {
    getAllNote,
    setIsClosing,
    isChanged,
    setIsChanged,
    isClosing,
    getSingleNote,
  } = useTaskContext();

  const navigate = useNavigate();

  useEffect(() => {
    setIsChanged(false);
    setIsLoading(true);
    getSingleNote(id);
    fakeId && navigate("/note");
    user || getAllNote();
    return setIsLoading(false);
  }, [fakeId]);

  return (
    <m.div
      variants={pageAnimationLeft}
      initial="hidden"
      animate="show"
      exit="exit"
      className="note-single-page"
    >
      <div className="nav-2">
        <m.div
          variants={buttonHoverTapSmall}
          whileHover="hover"
          whileTap="tap"
          className="nav-2__close"
          onClick={() => {
            isChanged ? setIsClosing(true) : navigate("/note");
          }}
        >
          CLOSE
        </m.div>
      </div>
      <NoteFormEdit></NoteFormEdit>
      {isClosing && <NoteSaveModule></NoteSaveModule>}
    </m.div>
  );
}
