import { Navigate, useNavigate } from "react-router-dom";
import { useGlobalContext } from "../context/appContext";
import { useAuthContext } from "../context/authContext";
import RegForm from "../components/Register/RegForm";

import { motion as m } from "framer-motion";
import {
  pageAnimationLeft,
  nav1,
  nav1HomeButton,
  authRest,
} from "../animations/animations";
import { useEffect } from "react";

export default function Register() {
  const { setIsLoading, setError } = useGlobalContext();

  const { isUserLoggedIn, setShowPassword, setUpdateUser } = useAuthContext();

  useEffect(() => {
    setIsLoading(false);
    setError(null);
    setShowPassword(false);
    setUpdateUser(null);
  }, []);

  const navigate = useNavigate();

  return isUserLoggedIn() ? (
    <Navigate to="/dashboard"></Navigate>
  ) : (
    <m.div
      variants={pageAnimationLeft}
      initial="hidden"
      animate="show"
      exit="exit"
      className="auth-page"
    >
      <m.nav variants={nav1} initial="hidden" animate="show" className="nav-1">
        <m.a
          variants={nav1HomeButton}
          onClick={() => {
            navigate("/");
          }}
          className="nav-1__link"
        >
          NOTERRA
        </m.a>
      </m.nav>

      <m.header variants={authRest} className="auth__header__container">
        <span className="register auth__header__text">REGISTER</span>
        <span className="register auth__header__dot">.</span>
      </m.header>
      <RegForm />
    </m.div>
  );
}
