import { useNavigate, Navigate } from "react-router-dom";
import { useGlobalContext } from "../context/appContext";
import { useAuthContext } from "../context/authContext";

import background from "../assets/images/bg-1.jpg";

import { motion as m, useScroll, useTransform } from "framer-motion";
import {
  pageAnimationLeft,
  nav1,
  nav1HomeButton,
  nav1LinkContainer,
  nav1LinkContainerButtons,
  headerBody,
  headerTitle,
  headerRest,
  cardAnimate,
  cardAnimateTexts,
  v1Logo,
  buttonCta,
} from "../animations/animations";
import { useEffect } from "react";

export default function Home() {
  const navigate = useNavigate();
  const { getQuote, quote, author } = useGlobalContext();
  const { isUserLoggedIn } = useAuthContext();

  const { scrollYProgress } = useScroll();

  const x = useTransform(scrollYProgress, [0, 1], [0, 560]);
  const slowX1 = useTransform(scrollYProgress, [0, 1], [0, 60]);
  const slowX2 = useTransform(scrollYProgress, [0, 1], [0, 120]);
  const slowX3 = useTransform(scrollYProgress, [0, 1], [0, 180]);
  const negX = useTransform(scrollYProgress, [0, 1], [0, 260]);

  useEffect(() => {
    getQuote();
  }, []);

  return isUserLoggedIn() ? (
    <Navigate to="/dashboard"></Navigate>
  ) : (
    <m.div
      variants={pageAnimationLeft}
      initial="hidden"
      animate="show"
      exit="exit"
      className="home-page"
    >
      <m.nav variants={nav1} initial="hidden" animate="show" className="nav-1">
        <m.a variants={nav1HomeButton} className="nav-1__link" href="/">
          NOTERRA
        </m.a>

        <m.div variants={nav1LinkContainer} className="nav__links">
          <m.button
            variants={nav1LinkContainerButtons}
            onClick={() => navigate("/login")}
            className="nav-1__cta-1"
          >
            LOGIN
          </m.button>
          <m.button
            variants={nav1LinkContainerButtons}
            onClick={() => navigate("/register")}
            className="nav-1__cta-2"
          >
            SIGN UP
          </m.button>
        </m.div>
      </m.nav>
      <m.div
        variants={headerBody}
        initial="hidden"
        animate="show"
        className="header__container"
      >
        <m.h1 variants={headerTitle} className="header__main">
          NOTERRA
        </m.h1>
        <m.div variants={headerTitle} className="subheader__container">
          <p className="subheader__quote">{quote}</p>
          <p className="subheader__author">-{author}r</p>
        </m.div>
        <m.div
          variants={headerRest}
          initial="hidden"
          animate="show"
          className="cta__container"
        >
          <m.button
            className="cta__button--signup"
            onClick={() => {
              navigate("/register");
            }}
          >
            SIGN UP
          </m.button>
          <m.button
            className="cta__button--login"
            onClick={() => {
              navigate("/login");
            }}
          >
            SIGN IN
          </m.button>
          <div className="cta__line"></div>
        </m.div>
        <m.div
          variants={headerRest}
          initial="hidden"
          animate="show"
          className="home-image-container"
        >
          <img className="home-image" src={background} alt="bg"></img>
          <m.div
            variants={v1Logo}
            animate="show"
            whileTap="tap"
            drag
            dragConstraints={{
              top: -10,
              left: -10,
              right: 10,
              bottom: 10,
            }}
            className="home-version"
          >
            <div> v1</div>
          </m.div>
        </m.div>
      </m.div>
      <m.div
        variants={headerRest}
        initial="hidden"
        animate="show"
        className="home-sliding-text__container"
      >
        <m.h1 style={{ x }} className="home-sliding-text__text">
          WRITE OUT YOUR PLANS&nbsp;&nbsp;&nbsp;&nbsp; |
          &nbsp;&nbsp;&nbsp;&nbsp;WRITE OUT YOUR PLANS&nbsp;&nbsp;&nbsp;&nbsp;
          |&nbsp;&nbsp;&nbsp;&nbsp; WRITE YOUR PLANS&nbsp;&nbsp;&nbsp;&nbsp; |
          &nbsp;&nbsp;&nbsp;&nbsp;WRITE YOUR PLANS&nbsp;&nbsp;&nbsp;&nbsp; |
          &nbsp;&nbsp;&nbsp;&nbsp;WRITE YOUR PLANS&nbsp;&nbsp;&nbsp;&nbsp; |
          &nbsp;&nbsp;&nbsp;&nbsp;WRITE YOUR PLANS&nbsp;&nbsp;&nbsp;&nbsp; |
          &nbsp;&nbsp;&nbsp;&nbsp;WRITE YOUR PLANS&nbsp;&nbsp;&nbsp;&nbsp; |
          &nbsp;&nbsp;&nbsp;&nbsp;WRITE YOUR PLANS&nbsp;&nbsp;&nbsp;&nbsp; |
          &nbsp;&nbsp;&nbsp;&nbsp;WRITE YOUR PLANS
        </m.h1>
      </m.div>
      <m.div
        variants={headerRest}
        initial="hidden"
        animate="show"
        className="features__heading"
      >
        <m.div className="features__heading-row1">
          <m.div className="features__headline">FEATURES</m.div>
          <m.div className="features__headline-dot">.</m.div>
          <button
            className="features__cta"
            onClick={() => {
              navigate("/register");
            }}
          >
            CREATE AN ACCOUNT
          </button>
        </m.div>
        <m.p className="features__headline-paragraph">
          The app features three important methods for productivity which are
          taking down notes, writing todo lists, and using the Pomodoro timer.
        </m.p>
      </m.div>

      <m.div
        variants={cardAnimate}
        initial="hidden"
        whileInView="show"
        viewport={{ once: true, amount: 1 }}
        className="features__body"
      >
        <m.div
          style={{ x: slowX1 }}
          variants={cardAnimateTexts}
          className="features__notes__container"
        >
          <div className="features__notes">notes</div>
          <div className="features__notes-dot">.</div>
        </m.div>
        <m.div
          style={{ x: slowX2 }}
          variants={cardAnimateTexts}
          className="features__notes__container"
        >
          <div className="features__notes">todo</div>
          <div className="features__notes-dot">.</div>
        </m.div>
        <m.div
          style={{ x: slowX3 }}
          variants={cardAnimateTexts}
          className="features__notes__container"
        >
          <div className="features__notes">pomo</div>
          <div className="features__notes-dot">.</div>
        </m.div>
        <div className="features__notes__container">
          <div className="features__notes">&nbsp; </div>
        </div>
      </m.div>

      <m.div className="home-sliding-text__container">
        <m.h1 style={{ x: negX }} className="home-sliding-text__text">
          WRITE OUT YOUR PLANS&nbsp;&nbsp;&nbsp;&nbsp; |
          &nbsp;&nbsp;&nbsp;&nbsp;WRITE OUT YOUR PLANS&nbsp;&nbsp;&nbsp;&nbsp;
          |&nbsp;&nbsp;&nbsp;&nbsp; WRITE YOUR PLANS&nbsp;&nbsp;&nbsp;&nbsp; |
          &nbsp;&nbsp;&nbsp;&nbsp;WRITE YOUR PLANS&nbsp;&nbsp;&nbsp;&nbsp; |
          &nbsp;&nbsp;&nbsp;&nbsp;WRITE YOUR PLANS&nbsp;&nbsp;&nbsp;&nbsp; |
          &nbsp;&nbsp;&nbsp;&nbsp;WRITE YOUR PLANS
        </m.h1>
      </m.div>
      <div className="ready__container">
        <m.div
          variants={headerTitle}
          initial="hidden"
          whileInView="show"
          viewport={{ once: true, amount: 0.4 }}
          className="ready__text"
        >
          READY TO GET STARTED?
        </m.div>
        <m.button
          variants={buttonCta}
          animate="show"
          whileHover="hover"
          whileTap="tap"
          drag
          dragConstraints="drag"
          className="ready__cta__button--signup"
          onClick={() => {
            navigate("/register");
          }}
        >
          SIGN UP
        </m.button>
        <button
          className="ready__cta__button--login"
          onClick={() => {
            navigate("/login");
          }}
        >
          SIGN IN
        </button>
      </div>
      <footer>
        <div className="designed-by">designed and coded by</div>
        <div className="janille">janille sy</div>
        <div className="email">syjanille@gmail.com</div>
      </footer>
    </m.div>
  );
}
