import { useLocation, Routes, Route } from "react-router-dom";
import ProtectedRoutes from "./ProtectedRoutes";
import Dashboard from "./Dashboard";
import Notes from "./Notes";
import NotesSingle from "./NotesSingle";
import NotesSingleNew from "./NotesSingleNew";
import Todo from "./Todo";
import Pomodoro from "./Pomodoro";
import PomodoroSingle from "./PomodoroSingle";

import Home from "./Home";
import Error from "./Error";
import Register from "./Register";
import Login from "./Login";

import { AnimatePresence } from "framer-motion";

function AnimatedRoutes(props) {
  const location = useLocation();
  return (
    <AnimatePresence>
      <Routes location={location} key={location.pathname}>
        <Route element={<ProtectedRoutes></ProtectedRoutes>}>
          <Route path="/dashboard" element={<Dashboard></Dashboard>}></Route>
          <Route path="/note" element={<Notes></Notes>}></Route>
          <Route
            path="/note/new"
            element={<NotesSingleNew></NotesSingleNew>}
          ></Route>
          <Route path="/note/:id" element={<NotesSingle></NotesSingle>}></Route>
          <Route path="/todo" element={<Todo></Todo>}></Route>
          <Route path="/pomodoro" element={<Pomodoro></Pomodoro>}></Route>
          <Route
            path="/pomodoro/:id"
            element={<PomodoroSingle></PomodoroSingle>}
          ></Route>
        </Route>

        <Route path="/" element={<Home></Home>}></Route>
        <Route path="/login" element={<Login></Login>}></Route>
        <Route path="/register" element={<Register></Register>}></Route>
        <Route path="*" element={<Error></Error>}></Route>
      </Routes>
    </AnimatePresence>
  );
}

export default AnimatedRoutes;
