import { useForm } from "react-hook-form";
import { Link } from "react-router-dom";
import { useGlobalContext } from "../../context/appContext";
import { useAuthContext } from "../../context/authContext";
import defaultImage from "../../assets/images/default.jpg";

import { motion as m } from "framer-motion";
import { authForm, buttonHoverTap } from "../../animations/animations";

import { RiEyeCloseLine, RiEyeLine } from "react-icons/ri";

export default function RegForm() {
  const { error, setError, isLoading } = useGlobalContext();
  const {
    showPassword,
    registerUser,
    setShowPassword,
    updateUser,
    tempoUploadImage,
  } = useAuthContext();
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();

  return (
    <m.div
      variants={authForm}
      animate="show"
      initial="hidden"
      className="auth-form-container"
    >
      <div className="auth-form__subtext__container">
        <div className="auth-form__subtext__text">ALREADY HAVE AN ACCOUNT?</div>
        <Link to="/login" className="auth-form__subtext__link">
          SIGN IN
        </Link>
      </div>
      <form
        className="auth-form"
        onSubmit={handleSubmit(async (data) => {
          await registerUser(data);

          reset();
        })}
      >
        <img
          className="auth-image"
          src={updateUser?.image || defaultImage}
          alt="profile-pic"
        />

        <input
          className="edit-input__input"
          {...register("image", {
            onChange: (e) => {
              tempoUploadImage(e.target.files);
            },
          })}
          type="file"
          accept="image/png, image/jpeg"
        />

        <label className="form__label" htmlFor="reg-name">
          USERNAME
          <input
            className={
              errors.name?.message || error
                ? "form__input invalid"
                : "form__input"
            }
            {...register("name", {
              required: "Input name",
              onChange: () => setError(null),
              minLength: {
                value: 3,
                message: "Minimum of 3 characters",
              },
              maxLength: {
                value: 8,
                message: "Max 8 characters",
              },
            })}
            id="reg-name"
            placeholder="Input username..."
          />
        </label>

        <label className="form__label" htmlFor="reg-email">
          EMAIL
          <input
            className={
              errors.email?.message || error
                ? "form__input invalid"
                : "form__input"
            }
            {...register("email", {
              onChange: () => setError(null),
              required: "Input email",
              pattern: {
                value:
                  /^([a-z\d!#$%&'*+\-\/=?^_`{|}~\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]+(\.[a-z\d!#$%&'*+\-\/=?^_`{|}~\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]+)*|"((([ \t]*\r\n)?[ \t]+)?([\x01-\x08\x0b\x0c\x0e-\x1f\x7f\x21\x23-\x5b\x5d-\x7e\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]|\\[\x01-\x09\x0b\x0c\x0d-\x7f\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]))*(([ \t]*\r\n)?[ \t]+)?")@(([a-z\d\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]|[a-z\d\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF][a-z\d\-._~\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]*[a-z\d\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])\.)+([a-z\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]|[a-z\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF][a-z\d\-._~\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]*[a-z\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])\.?$/i,
                message: "Input proper email",
              },
            })}
            id="reg-email"
            placeholder="Input email..."
          />
        </label>

        <label htmlFor="login-password" className="form__label">
          PASSWORD
          <input
            className={
              errors.password?.message || error
                ? "form__input password invalid"
                : "form__input password"
            }
            id="login-password"
            {...register("password", {
              required: "Input Password",
              onChange: () => setError(null),
            })}
            type={showPassword ? "text" : "password"}
            placeholder={
              errors.password?.message
                ? "Use valid password..."
                : "Input password..."
            }
          ></input>
          <div
            className="form__btn-showPassword"
            onClick={() => {
              setShowPassword((prev) => !prev);
            }}
          >
            {!showPassword ? <RiEyeCloseLine /> : <RiEyeLine />}
          </div>
        </label>

        <m.button
          whileHover="hover"
          whileTap="tap"
          variants={buttonHoverTap}
          type="submit"
          className="form__btn--submit"
          disabled={isLoading}
        >
          {isLoading ? "LOADING..." : "SIGN UP"}
        </m.button>
      </form>

      {(errors.password?.message ||
        errors.email?.message ||
        errors.name?.message ||
        error) && (
        <div className="error__container">
          {errors.email?.message && <p>{errors.email?.message}</p>}
          {errors.name?.message && <p>{errors.name?.message}</p>}
          {errors.password?.message && <p>{errors.password?.message}</p>}
          {error && <p>{error}</p>}
        </div>
      )}
    </m.div>
  );
}
