import React, { useContext, useState } from "react";
import axios from "axios";

const AppContext = React.createContext();
const { REACT_APP_API_KEY } = process.env;

function AppProvider({ children }) {
  // axios.defaults.withCredentials = true;
  const url = REACT_APP_API_KEY;
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [search, setSearch] = useState("");
  const [favorite, setFavorite] = useState(null);
  const [fakeId, setFakeId] = useState(false);
  const [quote, setQuote] = useState("");
  const [author, setAuthor] = useState("");
  const [editMode, setEditMode] = useState(false);

  const getQuote = async () => {
    const res = await axios.get("https://type.fit/api/quotes", {
      withCredentials: false,
    });
    const filterQuotes = res.data.filter((e) => {
      return e.text.length < 200;
    });
    const allQuotes = filterQuotes.filter((e) => {
      return e.text.length > 110;
    });

    let selectedQuote = allQuotes[Math.floor(Math.random() * allQuotes.length)];
    if (!selectedQuote.author) {
      selectedQuote.author = "Anonymous";
    }
    setQuote(selectedQuote.text);
    setAuthor(selectedQuote.author);
  };

  return (
    <AppContext.Provider
      value={{
        url,
        isLoading,
        setIsLoading,
        search,
        setSearch,
        editMode,
        setEditMode,
        error,
        setError,
        favorite,
        setFavorite,
        getQuote,
        author,
        quote,
        fakeId,
        setFakeId,
        setAuthor,
      }}
    >
      {children}
    </AppContext.Provider>
  );
}

export const useGlobalContext = () => {
  return useContext(AppContext);
};

export { AppContext, AppProvider };
