import { useTaskContext } from "../../context/taskContext";
import { useNavigate } from "react-router-dom";
import { ImPlay3 } from "react-icons/im";
import { motion as m } from "framer-motion";
import { listSlideLeft, buttonHoverTap } from "../../animations/animations";
import { useGlobalContext } from "../../context/appContext";

export default function PomodoroItem({ pomodoro }) {
  const { setFakeId } = useGlobalContext();
  const { deletePomodoro } = useTaskContext();
  const navigate = useNavigate();

  return (
    <m.div
      variants={listSlideLeft}
      initial="hidden"
      animate="show"
      exit="exit"
      whileHover="hover"
      key={pomodoro._id}
      className="pomo-item"
    >
      <button
        className="pomo-item__play-container"
        onClick={() => {
          setFakeId(false);
          navigate("/pomodoro/" + pomodoro._id);
        }}
      >
        <ImPlay3 className="pomo-item__play" />
      </button>
      <div className="pomo-item__right">
        <div className="pomo-item__title-delete">
          <div
            className={
              pomodoro.favorite ? "pomo-item__title fave" : "pomo-item__title"
            }
          >
            {pomodoro.title}
          </div>
          <m.button
            variants={buttonHoverTap}
            whileHover="hover"
            whileTap="tap"
            className="pomo-item__delete"
            onClick={() => {
              deletePomodoro(pomodoro._id);
            }}
          >
            x
          </m.button>
        </div>
        <div className="pomo-item__value">
          <div>TOTAL: {pomodoro.value + ""}</div>
        </div>
      </div>
    </m.div>
  );
}
