import { useEffect } from "react";
import { useForm } from "react-hook-form";
import { motion as m } from "framer-motion";
import { useGlobalContext } from "../../context/appContext";
import { useTaskContext } from "../../context/taskContext";
import {
  buttonHoverTap,
  buttonHoverTapSmall,
} from "../../animations/animations";

export default function NoteFormNew() {
  const { setIsLoading } = useGlobalContext();
  const {
    singleNote,
    isNewNote,
    isChanged,
    setIsChanged,
    updateNote,
    addNote,
  } = useTaskContext();

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  useEffect(() => {
    setIsLoading(true);
    reset(singleNote);
    return setIsLoading(false);
  }, [singleNote]);

  return (
    <form
      className="note-form"
      onSubmit={handleSubmit(async (data) => {
        if (isNewNote) {
          setIsLoading(true);
          addNote(data);
          setIsLoading(false);
        } else {
          setIsLoading(true);
          updateNote(data);
          setIsLoading(false);
        }
      })}
    >
      <input
        className="note-form__title"
        {...register(
          "title",

          {
            required: "Input title",
            minLength: {
              value: 3,
              message: "Title must have of at least 3 characters",
            },
            onChange: () => !isChanged && setIsChanged(true),
          }
        )}
        placeholder="Title..."
      />
      <div className="note-subheader__container">
        <m.div
          variants={buttonHoverTapSmall}
          whileHover="hover"
          whileTap="tap"
          className="favorite-container"
        >
          <label htmlFor="favoriteSingleNote" className="favorite-label">
            <input
              type="checkbox"
              className="favorite-check"
              id="favoriteSingleNote"
              {...register(
                "favorite",

                { onChange: () => !isChanged && setIsChanged(true) }
              )}
            />
            <span className="favorite-span">FAVORITE</span>
          </label>
        </m.div>
        <div className="form-date">{new Date().toDateString()}</div>
      </div>
      <textarea
        className="note-form__notes"
        {...register("notes", {
          required: "Input notes",
          minLength: {
            value: 3,
            message: "Notes must have of at least 3 characters",
          },
        })}
        placeholder="Notes..."
      />{" "}
      <m.button
        variants={buttonHoverTap}
        whileHover="hover"
        whileTap="tap"
        type="submit"
        className={isChanged ? "note-form__save unsaved" : "note-form__save"}
      >
        {isChanged ? "SAVE" : "SAVE"}
      </m.button>
      {errors.title?.message && (
        <p className="note-error">{errors.title?.message}</p>
      )}
      {errors.notes?.message && (
        <p className="note-error">{errors.notes?.message}</p>
      )}
    </form>
  );
}
