import { useNavigate } from "react-router-dom";
import { useTaskContext } from "../../context/taskContext";
import { useGlobalContext } from "../../context/appContext";
import { motion as m } from "framer-motion";
import { listSlideLeft, buttonHoverTap } from "../../animations/animations";

export default function NoteItem({ note }) {
  const { deleteNote } = useTaskContext();
  const { setFakeId } = useGlobalContext();
  const navigate = useNavigate();

  return (
    <m.div
      variants={listSlideLeft}
      initial="hidden"
      animate="show"
      exit="exit"
      whileHover="hover"
      className={note.favorite ? "note-item fave" : "note-item"}
      key={note._id}
    >
      <div
        onClick={() => {
          setFakeId(false);
          navigate(`/note/${note._id}`);
        }}
        className="note-item__title"
      >
        {note.title}
      </div>

      <div
        onClick={() => {
          setFakeId(false);
          navigate(`/note/${note._id}`);
        }}
        className="note-item__paragraph"
      >
        {note.notes.substring(0, 30) + "..."}
      </div>

      <m.button
        variants={buttonHoverTap}
        whileHover="hover"
        whileTap="tap"
        className="note-item__delete"
        onClick={() => {
          deleteNote(note._id);
        }}
      >
        x
      </m.button>
    </m.div>
  );
}
