import { useGlobalContext } from "../context/appContext";
import { useTaskContext } from "../context/taskContext";
import { useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";

import NoteItem from "../components/Note/NoteItem";

import { motion as m, AnimatePresence } from "framer-motion";
import {
  pageAnimationLeft,
  buttonHoverTapSmall,
  addButtonNotes,
} from "../animations/animations";

export default function Notes() {
  const { search, setSearch, setIsLoading, setFavorite, favorite, setFakeId } =
    useGlobalContext();

  const { notes, getAllNote, setSingleNote } = useTaskContext();

  const navigate = useNavigate();

  const searchWord = useRef("");

  useEffect(() => {
    setIsLoading(true);
    setFakeId(false);
    getAllNote();
    setSearch("");
    setFavorite(null);
    setSingleNote(null);
    setIsLoading(false);
  }, []);

  return (
    <>
      <m.div
        variants={pageAnimationLeft}
        initial="hidden"
        animate="show"
        exit="exit"
        className="note-page"
      >
        <div className="nav-2">
          <m.div
            className="nav-2__close"
            variants={buttonHoverTapSmall}
            whileHover="hover"
            whileTap="tap"
            onClick={() => {
              navigate("/dashboard");
            }}
          >
            CLOSE
          </m.div>
        </div>
        <div className="page__title">NOTES</div>
        <form
          className="filter__container"
          onSubmit={(e) => {
            e.preventDefault();
            setSearch(searchWord.current);
          }}
        >
          <input
            className="filter__input"
            placeholder="Filter..."
            onChange={(e) => {
              searchWord.current = e.target.value;
              searchWord.current === "" && setSearch("");
            }}
          ></input>
          <m.button
            variants={buttonHoverTapSmall}
            whileHover="hover"
            whileTap="tap"
            className="filter__button"
            type="submit"
          >
            ?
          </m.button>
        </form>

        <m.div
          variants={buttonHoverTapSmall}
          whileHover="hover"
          whileTap="tap"
          className="favorite-container"
        >
          <label htmlFor="favoriteSingleNote" className="favorite-label">
            <input
              type="checkbox"
              className="favorite-check"
              id="favoriteSingleNote"
              onChange={(e) => {
                const isFavorite = e.target.checked || null;
                setFavorite(isFavorite);
              }}
            />
            <span className="favorite-span">FAVORITES</span>
          </label>
        </m.div>
        <br />

        <AnimatePresence mode="sync">
          {notes
            .filter((note) => {
              return (
                note.title.toLowerCase().includes(search.toLowerCase()) ||
                note.notes.toLowerCase().includes(search.toLowerCase())
              );
            })
            .filter((note) => {
              if (favorite === null) {
                return note;
              } else {
                return note.favorite === favorite;
              }
            })
            .map((note) => {
              return <NoteItem note={note} key={note._id}></NoteItem>;
            })}
        </AnimatePresence>

        <div className="note-footer__empty"></div>
      </m.div>
      <m.button
        variants={addButtonNotes}
        initial="hidden"
        animate="show"
        exit="exit"
        whileHover="hover"
        whileTap="tap"
        className="note-add-button"
        onClick={() => navigate("/note/new")}
      >
        ADD NEW NOTE
      </m.button>
      <m.footer
        variants={pageAnimationLeft}
        initial="hidden"
        animate="show"
        exit="exit"
        className="footer__container"
      >
        <div className="footer__text">TOTAL NOTES: {notes.length}</div>
      </m.footer>
    </>
  );
}
