import React, { useContext, useState } from "react";
import axios from "axios";
import { useGlobalContext } from "./appContext";
import { useAuthContext } from "./authContext";

const TaskContext = React.createContext();

function TaskProvider({ children }) {
  const { setIsLoading } = useGlobalContext();
  const { setTokenUser, setUser, setFakeId, fakeId } = useAuthContext();

  const [finished, setFinished] = useState(null);

  const [notes, setNotes] = useState([]);
  const [singleNote, setSingleNote] = useState(null);
  const [isNewNote, setIsNewNote] = useState(true);

  const [singleTodo, setSingleTodo] = useState(null);
  const [todos, setTodos] = useState([]);

  const [pomodoros, setPomodoros] = useState([]);
  const [singlePomodoro, setSinglePomodoro] = useState(null);

  const [isClosing, setIsClosing] = useState(false);

  const [isChanged, setIsChanged] = useState(false);

  const [focusTimeSeconds, setFocusTimeSeconds] = useState(1200);
  const [restTimeSeconds, setRestTimeSeconds] = useState(60);
  const [isRest, setIsRest] = useState(null);
  const [percent, setPercent] = useState(0);

  const getAllData = async () => {
    try {
      let allTodo = await axios.get("/api/todo");
      let allNote = await axios.get("/api/note");
      let allPomodoro = await axios.get("/api/pomodoro");

      setTodos([...allTodo.data.sortedTodoList]);
      setNotes([...allNote.data.sortedNoteList]);
      setPomodoros([...allPomodoro.data.sortedPomodoroList]);
    } catch (error) {
      localStorage.setItem("login", false);
      setTokenUser(false);
      setUser(null);
    }
  };

  const getAllNote = async () => {
    try {
      let allNote = await axios.get("/api/note");
      setNotes([...allNote.data.sortedNoteList]);
    } catch (error) {
      localStorage.setItem("login", false);
      setTokenUser(false);
      setUser(null);
    }
  };

  const addNote = async (data) => {
    const res = await axios.post("/api/note/", data);
    getAllNote();
    setSingleNote(res.data);
    setIsNewNote(false);
    setIsChanged(false);
  };

  const updateNote = async (data) => {
    await axios.patch("/api/note/" + singleNote._id, data);
    getAllNote();
    setSingleNote(data);
    setIsChanged(false);
  };

  const getSingleNote = async (id) => {
    try {
      let single = await axios.get("/api/note?noteId=" + id);
      setSingleNote(single.data.sortedNoteList[0]);

      return true;
    } catch (error) {
      setFakeId(true);
      return null;
    }
  };

  const deleteNote = async (id) => {
    setIsLoading(true);
    let updatedNotes = notes.filter((note) => {
      return note._id !== id;
    });

    setNotes(updatedNotes);

    await axios.delete("/api/note/" + id);
    setIsLoading(false);
  };

  const getAllTodo = async () => {
    try {
      let allTodo = await axios.get("/api/todo");
      setTodos([...allTodo.data.sortedTodoList]);
    } catch (error) {
      localStorage.setItem("login", false);
      setTokenUser(false);
      setUser(null);
    }
  };
  const addTodo = async (data) => {
    await axios.post("/api/todo/", data);
    getAllTodo();
  };
  const finishTodo = async (id) => {
    setIsLoading(true);
    let isFinished;
    const updatedTodo = todos.map((todo) => {
      if (todo._id === id) {
        isFinished = !todo.finished;
        return { ...todo, finished: !todo.finished };
      } else {
        return todo;
      }
    });
    setTodos(updatedTodo);

    await axios.patch("/api/todo/" + id, {
      finished: isFinished,
    });
    setIsLoading(false);
  };
  const deleteTodo = async (id) => {
    setIsLoading(true);
    let updatedTodo = todos.filter((todo) => {
      return todo._id !== id;
    });

    setTodos(updatedTodo);

    await axios.delete("/api/todo/" + id);
    setIsLoading(false);
  };

  const getSingleTodo = (id) => {
    const singleTodo = todos.find((todo) => {
      return todo._id === id;
    });
    setSingleTodo(singleTodo);
  };

  const updateTodo = async (id) => {
    if (singleTodo.task.trim() === "") {
      return deleteTodo(id);
    }

    const updatedTodo = todos.map((todo) => {
      if (todo._id === id) {
        return {
          ...todo,
          task: singleTodo.task.trim(),
          favorite: singleTodo.favorite,
        };
      } else {
        return todo;
      }
    });

    setTodos(updatedTodo);

    await axios.patch("/api/todo/" + id, {
      task: singleTodo.task.trim(),
      favorite: singleTodo.favorite,
    });
  };

  const getAllPomodoro = async () => {
    try {
      let allPomodoro = await axios.get("/api/pomodoro");
      setPomodoros([...allPomodoro.data.sortedPomodoroList]);
    } catch (error) {
      localStorage.setItem("login", false);
      setTokenUser(false);
      setUser(null);
    }
  };

  const getSinglePomodoro = async (id) => {
    try {
      let single = await axios.get("/api/pomodoro?pomoId=" + id);
      setSinglePomodoro(single.data.sortedPomodoroList[0]);
      return true;
    } catch (error) {
      setFakeId(true);
      return null;
    }
  };

  const addPomodoro = async (data) => {
    await axios.post("/api/pomodoro/", data);
    getAllPomodoro();
  };

  const startPomodoro = async (id) => {
    const res = await axios.patch("/api/pomodoro/" + id, {
      value: singlePomodoro.value + 1,
    });
    const newValue = res.data.updatedPomodoro.value;

    setSinglePomodoro((prev) => {
      return { ...prev, value: newValue };
    });
    getAllPomodoro();
  };

  const updatePomodoro = async (id, title, favorite) => {
    setSinglePomodoro((prev) => {
      return { ...prev, title, favorite };
    });

    let updatedPomodoro = pomodoros.map((pomodoro) => {
      if (pomodoro._id === id) {
        return { ...pomodoro, title, favorite: favorite };
      } else {
        return pomodoro;
      }
    });
    setPomodoros(updatedPomodoro);

    await axios.patch("/api/pomodoro/" + id, {
      title,
      favorite,
    });
  };

  const resetPomodoro = async (id) => {
    setSinglePomodoro((prev) => {
      return { ...prev, value: 0 };
    });

    let updatedPomodoro = pomodoros.map((pomodoro) => {
      if (pomodoro._id === id) {
        return { ...pomodoro, value: 0 };
      } else {
        return pomodoro;
      }
    });
    setPomodoros(updatedPomodoro);

    await axios.patch("/api/pomodoro/" + id, {
      value: 0,
    });
  };

  const deletePomodoro = async (id) => {
    setIsLoading(true);
    let updatedPomodoro = pomodoros.filter((pomodoro) => {
      return pomodoro._id !== id;
    });

    setPomodoros(updatedPomodoro);

    await axios.delete("/api/pomodoro/" + id);
    setIsLoading(false);
  };

  const favoritePomodoro = async (id) => {
    setIsLoading(true);
    let isFavorite;
    const updatedPomodoro = pomodoros.map((pomodoro) => {
      if (pomodoro._id === id) {
        isFavorite = !pomodoro.favorite;
        return { ...pomodoro, favorite: !pomodoro.favorite };
      } else {
        return pomodoro;
      }
    });
    setPomodoros(updatedPomodoro);

    await axios.patch("/api/pomodoro/" + id, {
      favorite: isFavorite,
    });
    setIsLoading(false);
  };

  return (
    <TaskContext.Provider
      value={{
        isClosing,
        setIsClosing,

        getAllData,
        notes,
        setNotes,
        todos,
        setTodos,
        pomodoros,
        setPomodoros,

        getAllNote,
        getSingleNote,
        singleNote,
        isNewNote,
        setIsNewNote,
        deleteNote,
        setSingleNote,
        updateNote,
        addNote,

        getAllTodo,
        addTodo,
        finishTodo,
        deleteTodo,
        getSingleTodo,
        singleTodo,
        setSingleTodo,
        updateTodo,

        singlePomodoro,
        setSinglePomodoro,

        addPomodoro,
        getAllPomodoro,
        getSinglePomodoro,
        startPomodoro,
        updatePomodoro,
        resetPomodoro,
        deletePomodoro,
        favoritePomodoro,
        percent,
        setPercent,

        focusTimeSeconds,
        restTimeSeconds,
        isRest,
        setIsRest,
        setFocusTimeSeconds,
        setRestTimeSeconds,

        setFakeId,
        fakeId,
        isChanged,
        setIsChanged,
        finished,
        setFinished,
      }}
    >
      {children}
    </TaskContext.Provider>
  );
}

export const useTaskContext = () => {
  return useContext(TaskContext);
};

export { TaskContext, TaskProvider };
