import { useNavigate } from "react-router-dom";
import { useGlobalContext } from "../context/appContext";
import { useTaskContext } from "../context/taskContext";
import { useAuthContext } from "../context/authContext";
import { useEffect, useRef } from "react";
import { useForm } from "react-hook-form";

import TodoItem from "../components/Todo/TodoItem";
import TodoEdit from "../components/Todo/TodoEdit";

import { motion as m, AnimatePresence } from "framer-motion";
import {
  pageAnimationLeft,
  buttonHoverTapSmall,
} from "../animations/animations";

export default function Todo() {
  const {
    search,
    setSearch,
    setIsLoading,
    setEditMode,
    editMode,
    favorite,
    setFavorite,
  } = useGlobalContext();
  const { user } = useAuthContext();
  const { todos, getAllTodo, finished, setFinished, addTodo } =
    useTaskContext();

  const searchWord = useRef("");

  useEffect(() => {
    setIsLoading(true);
    user || getAllTodo();
    setSearch("");
    setFavorite(null);
    setFinished(null);
    setIsLoading(false);
    setEditMode(false);
  }, []);

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();

  const navigate = useNavigate();

  return (
    <>
      <m.div
        variants={pageAnimationLeft}
        initial="hidden"
        animate="show"
        exit="exit"
        className="todo-page"
      >
        <div className="nav-2">
          <div
            className="nav-2__close"
            onClick={() => {
              navigate("/dashboard");
            }}
          >
            CLOSE
          </div>
        </div>
        <div className="page__title">TODO</div>

        <form
          className="filter__container"
          onSubmit={handleSubmit(async (data) => {
            setIsLoading(true);
            addTodo(data);
            reset();
            setIsLoading(false);
          })}
        >
          <input
            className="filter__input"
            {...register("task", {
              required: "Input task",
              minLength: {
                value: 3,
                message: "Task must have of at least 3 characters",
              },
            })}
            placeholder="Add task..."
          ></input>
          <m.button
            variants={buttonHoverTapSmall}
            whileHover="hover"
            whileTap="tap"
            className="filter__button"
            type="submit"
          >
            ADD
          </m.button>
        </form>

        <form
          className="filter__container"
          onSubmit={(e) => {
            e.preventDefault();
            setSearch(searchWord.current);
          }}
        >
          <input
            className="filter__input"
            placeholder="Filter..."
            onChange={(e) => {
              searchWord.current = e.target.value;
              searchWord.current === "" && setSearch("");
            }}
          ></input>
          <m.button
            variants={buttonHoverTapSmall}
            whileHover="hover"
            whileTap="tap"
            className="filter__button"
            type="submit"
          >
            ?
          </m.button>
        </form>

        <br />

        <div className="todo-filter-group">
          <m.div
            variants={buttonHoverTapSmall}
            whileHover="hover"
            whileTap="tap"
            className="favorite-container"
          >
            <label htmlFor="favoriteTodos" className="favorite-label">
              <input
                type="checkbox"
                className="favorite-check"
                id="favoriteTodos"
                onChange={(e) => {
                  const isFavorite = e.target.checked || null;
                  setFavorite(isFavorite);
                }}
              />
              <span className="favorite-span">FAVORITES</span>
            </label>
          </m.div>

          <select
            className="options__container"
            id="status"
            onChange={(e) => {
              if (e.target.value === "all") {
                setFinished(null);
              } else if (e.target.value === "unfinished") {
                setFinished(false);
              } else {
                setFinished(true);
              }
            }}
          >
            <option value="all" className="options__item">
              All
            </option>
            <option value="unfinished" className="options__item">
              Unfinished
            </option>
            <option value="finished" className="options__item">
              Finished
            </option>
          </select>
        </div>
        <br />
        <AnimatePresence>
          {todos
            .filter((todo) => {
              return todo.task.toLowerCase().includes(search.toLowerCase());
            })
            .filter((todo) => {
              if (favorite === null) {
                return todo;
              } else {
                return todo.favorite === favorite;
              }
            })
            .filter((todo) => {
              if (finished === null) {
                return todo;
              } else if (finished) {
                return todo.finished === true;
              } else if (!finished) {
                return todo.finished === false;
              }
            })
            .map((todo) => {
              return <TodoItem todo={todo} key={todo._id}></TodoItem>;
            })}
          {editMode && <TodoEdit></TodoEdit>}
        </AnimatePresence>

        <div className="footer__empty"></div>
      </m.div>

      <m.footer
        variants={pageAnimationLeft}
        initial="hidden"
        animate="show"
        exit="exit"
        className="footer__container"
      >
        <div className="footer__text">TOTAL TODO: {todos.length}</div>
      </m.footer>
    </>
  );
}
