import { useForm } from "react-hook-form";

import { useGlobalContext } from "../../context/appContext";
import { useAuthContext } from "../../context/authContext";

import defaultImage from "../../assets/images/default.jpg";

import { motion as m } from "framer-motion";
import {
  pageOpacity,
  headerTitle,
  buttonHoverTapSmall,
} from "../../animations/animations";

export default function EditProfile() {
  const { error, isLoading, setEditMode } = useGlobalContext();
  const { user, updateUser, tempoUploadImage, setUpdateUser, editUser } =
    useAuthContext();

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();

  return (
    <m.div
      variants={pageOpacity}
      initial="hidden"
      animate="show"
      exit="exit"
      className="edit-form dashboard"
    >
      <m.form
        variants={headerTitle}
        exit={{ opacity: 0 }}
        className="edit-form__container"
        onSubmit={handleSubmit(async (data) => {
          if (data.name === "") {
            data.name = user.name;
          }
          await editUser(data);

          setEditMode(false);
          reset();
        })}
      >
        <m.div className="edit__top">
          <div className="edit__title">EDIT PROFILE</div>
        </m.div>
        <img
          className="edit-image"
          src={updateUser?.image || defaultImage}
          alt="profile-pic"
        />
        <input
          className="edit-input__input"
          {...register("image", {
            onChange: (e) => {
              tempoUploadImage(e.target.files);
            },
          })}
          type="file"
          accept="image/png, image/jpeg"
        />
        <label className="edit__task-label" htmlFor="edit-name">
          <span> Username</span>

          <input
            className={
              errors.name?.message || error
                ? "form__input invalid"
                : "form__input"
            }
            {...register("name", {
              onChange: (e) => {
                setUpdateUser((prev) => {
                  return { ...prev, name: e.target.value };
                });
              },
              minLength: {
                value: 3,
                message: "Minimum of 3 characters",
              },
              maxLength: {
                value: 8,
                message: "Max 8 characters",
              },
            })}
            // id="edit-name"
            placeholder="Input username..."
            defaultValue={updateUser.name}
          />
        </label>
        <div className="edit-buttons">
          <m.button
            variants={buttonHoverTapSmall}
            whileHover="hover"
            whileTap="tap"
            className="edit__submit"
            type="submit"
            disabled={isLoading ? true : false}
          >
            {isLoading ? "LOADING..." : "EDIT"}
          </m.button>
          <m.button
            variants={buttonHoverTapSmall}
            whileHover="hover"
            whileTap="tap"
            className="edit__exit"
            onClick={(e) => {
              e.preventDefault();
              setEditMode(false);
            }}
          >
            CANCEL
          </m.button>
        </div>
      </m.form>

      {errors.name && (
        <div className="edit__error">
          <div>{errors.name?.message}</div>
        </div>
      )}
    </m.div>
  );
}
