import React, { useContext, useState } from "react";
import axios from "axios";
import { useGlobalContext } from "./appContext";

const AuthContext = React.createContext();

function AuthProvider({ children }) {
  const { setIsLoading, setError } = useGlobalContext();

  const [user, setUser] = useState(null);
  const [tokenUser, setTokenUser] = useState(false);
  const [updateUser, setUpdateUser] = useState({});
  const [showPassword, setShowPassword] = useState(false);
  const [imageSource, setImageSource] = useState(
    "https://res.cloudinary.com/dlriwaule/image/upload/v1676308316/user-image-upload/tmp-14-1676308315277_dwrfzc.jpg"
  );

  const loginUser = async (data) => {
    try {
      setIsLoading(true);
      setError(null);
      let res = await axios.post("/api/user/login", data);
      const { _id, name, image, theme, message } = res.data;

      message && setError(message);
      setTokenUser(!!_id);
      _id ? setUser({ _id, name, image, theme }) : setUser(null);
      !message && localStorage.setItem("login", true);
      setIsLoading(false);
      return message;
    } catch (error) {
      setError(error.message);
      setIsLoading(false);
    }
  };

  const checkLogin = async () => {
    const res = await axios.get("/api/user/");
    const { message } = res.data;
    if (!message) {
      setTokenUser(true);
      setUser({ ...res.data });

      return tokenUser;
    } else {
      localStorage.setItem("login", false);
      setTokenUser(false);
      setUser(null);

      return null;
    }
  };
  const isUserLoggedIn = () => {
    const login = JSON.parse(localStorage.getItem("login"));
    return login;
  };

  const registerUser = async (data) => {
    try {
      setIsLoading(true);
      setError(null);

      let res = await axios.post("/api/user/register", {
        name: data.name,
        email: data.email,
        password: data.password,
        image: updateUser?.image || imageSource,
      });
      const { _id, name, image, message } = res.data;
      message && setError(message);
      await setTokenUser(!!_id);
      _id ? setUser({ _id, name, image }) : setUser(null);
      !message && localStorage.setItem("login", true);
      setIsLoading(false);
      return message;
    } catch (error) {
      setError(error.message);
      setUpdateUser(null);
      setIsLoading(false);
    }
  };

  const tempoUploadImage = async (data) => {
    setIsLoading(true);
    if (data.length) {
      let imageFormData = new FormData();
      imageFormData.append("image", data[0]);
      const res = await axios({
        method: "post",
        url: "/api/user/register/upload",
        data: imageFormData,
        headers: { "Content-Type": "multipart/form-data" },
      });
      setUpdateUser((prev) => ({ ...prev, image: res.data.image.src }));
    } else {
      if (tokenUser) {
        setUpdateUser((prev) => ({ ...prev, image: user.image }));
      } else {
        setUpdateUser(null);
      }
    }

    setIsLoading(false);
  };

  const logoutUser = async (arg) => {
    try {
      localStorage.setItem("login", false);
      setTokenUser(false);
      setUser(null);
      await axios.get("/api/user/logout");
    } catch (error) {}
  };

  const editUser = async (data) => {
    let res = await axios.patch("/api/user/edit", {
      name: data.name,
      image: updateUser.image,
    });
    const { message } = res.data;
    if (!message) {
      setTokenUser(true);
      setUser({ ...res.data });

      return tokenUser;
    } else {
      localStorage.setItem("login", false);
      setTokenUser(false);
      setUser(null);
      return null;
    }
  };

  //    index.js
  return (
    <AuthContext.Provider
      value={{
        loginUser,
        checkLogin,
        isUserLoggedIn,
        setShowPassword,
        showPassword,
        setUpdateUser,
        updateUser,
        registerUser,
        setImageSource,
        imageSource,
        tempoUploadImage,
        setUser,
        user,
        tokenUser,
        setTokenUser,
        logoutUser,
        editUser,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
}

export const useAuthContext = () => {
  return useContext(AuthContext);
};

export { AuthContext, AuthProvider };
