import { useNavigate } from "react-router-dom";
import { useGlobalContext } from "../context/appContext";
import { useTaskContext } from "../context/taskContext";
import { useAuthContext } from "../context/authContext";
import { useEffect, useRef } from "react";

import { useForm } from "react-hook-form";
import PomodoroItem from "../components/Pomodoro/PomodoroItem";

import { motion as m, AnimatePresence } from "framer-motion";
import {
  pageAnimationLeft,
  buttonHoverTap,
  buttonHoverTapSmall,
} from "../animations/animations";

export default function Pomodoro() {
  const { search, setSearch, setIsLoading, setFavorite, favorite, setFakeId } =
    useGlobalContext();
  const { user } = useAuthContext();
  const { pomodoros, addPomodoro, getAllPomodoro } = useTaskContext();

  const navigate = useNavigate();
  const searchWord = useRef("");

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();

  useEffect(() => {
    setIsLoading(true);
    setFakeId(false);
    user || getAllPomodoro();
    setSearch("");
    setFavorite(null);
    setIsLoading(false);
  }, []);

  return (
    <>
      <m.div
        variants={pageAnimationLeft}
        initial="hidden"
        animate="show"
        exit="exit"
        className="pomo-page"
      >
        <div className="nav-2">
          <m.div
            variants={buttonHoverTap}
            whileHover="hover"
            whileTap="tap"
            className="nav-2__close"
            onClick={() => {
              navigate("/dashboard");
            }}
          >
            CLOSE
          </m.div>
        </div>

        <div className="experimentalBody"></div>
        <div className="page__title">POMO</div>

        <form
          className="filter__container"
          onSubmit={handleSubmit(async (data) => {
            setIsLoading(true);
            addPomodoro(data);
            reset();
            setIsLoading(false);
          })}
        >
          <input
            className="filter__input"
            {...register("title", {
              required: "Input task",
              minLength: {
                value: 3,
                message: "Task must have of at least 3 characters",
              },
            })}
            placeholder="Add task..."
          ></input>
          <m.button
            variants={buttonHoverTapSmall}
            whileHover="hover"
            whileTap="tap"
            className="filter__button"
            type="submit"
          >
            ADD
          </m.button>
        </form>

        <form
          className="filter__container"
          onSubmit={(e) => {
            e.preventDefault();
            setSearch(searchWord.current);
          }}
        >
          <input
            className="filter__input"
            placeholder="Filter"
            onChange={(e) => {
              searchWord.current = e.target.value;
              searchWord.current === "" && setSearch("");
            }}
          ></input>
          <m.button
            variants={buttonHoverTapSmall}
            whileHover="hover"
            whileTap="tap"
            className="filter__button"
            type="submit"
          >
            ?
          </m.button>
        </form>

        <m.div
          variants={buttonHoverTapSmall}
          whileHover="hover"
          whileTap="tap"
          className="favorite-container"
        >
          <label htmlFor="allPomodoroFavorites" className="favorite-label">
            <input
              type="checkbox"
              className="favorite-check"
              id="allPomodoroFavorites"
              onChange={(e) => {
                const isFavorite = e.target.checked || null;
                setFavorite(isFavorite);
              }}
            />
            <span className="favorite-span">FAVORITES</span>
          </label>
        </m.div>
        <div className="pomo-items__container">
          <AnimatePresence>
            {pomodoros
              .filter((pomodoro) => {
                return pomodoro.title
                  .toLowerCase()
                  .includes(search.toLowerCase());
              })
              .filter((pomodoro) => {
                if (favorite === null) {
                  return pomodoro;
                } else {
                  return pomodoro.favorite === favorite;
                }
              })
              .map((pomodoro) => {
                return (
                  <PomodoroItem
                    pomodoro={pomodoro}
                    key={pomodoro._id}
                  ></PomodoroItem>
                );
              })}
          </AnimatePresence>
        </div>

        <div className="note-footer__empty"></div>
      </m.div>
      <m.footer
        variants={pageAnimationLeft}
        initial="hidden"
        animate="show"
        exit="exit"
        className="footer__container"
      >
        <div className="footer__text">TOTAL POMODORO: {pomodoros.length}</div>
      </m.footer>
    </>
  );
}
