import { useNavigate } from "react-router-dom";
import { motion as m } from "framer-motion";
import { pageOpacity } from "../animations/animations";

export default function Error() {
  const navigate = useNavigate();
  return (
    <m.div
      variants={pageOpacity}
      initial="hidden"
      animate="show"
      exit="exit"
      className="error-page"
    >
      <m.div className="error-page__header">404</m.div>
      <m.div className="error-page__text-1">SORRY</m.div>
      <m.div className="error-page__text-2">THERE'S NOTHING HERE</m.div>

      <m.div
        whileHover={{ scale: 1.05 }}
        whileTap={{ scale: 0.95 }}
        className="error-page__cta"
        onClick={() => {
          navigate("/");
        }}
      >
        GET BACK
      </m.div>
      <br />
      <br />
      <br />
      <br />

      <m.div
        animate={{
          scale: [1.4, 1, 1.4],
          x: ["0%", "10%", "0%"],
          y: ["0%", "10%", "0%"],
          rotate: [0, 30, 60, 90, 120, 150, 180, 210, 240, 270, 300, 330, 360],
        }}
        transition={{
          ease: "linear",
          duration: 5,

          repeat: Infinity,
          type: "spring",
        }}
        whileHover={{}}
        whileTap={{
          scale: 0.9,
          transition: { duration: 0.2 },
        }}
        drag
        dragConstraints={{
          top: -50,
          left: -50,
          right: 50,
          bottom: 50,
        }}
        className="header__version-container"
      >
        <div className="version__text">v1</div>
      </m.div>
    </m.div>
  );
}
