import { useNavigate, useParams } from "react-router-dom";
import { useGlobalContext } from "../context/appContext";
import { useAuthContext } from "../context/authContext";
import { useTaskContext } from "../context/taskContext";
import { useEffect, useRef, useState } from "react";
import PomodoroTimer from "../components/Pomodoro/PomodoroTimer";
import PomodoroEdit from "../components/Pomodoro/PomodoroEdit";

import { motion as m, AnimatePresence } from "framer-motion";
import { pageAnimationLeft, buttonHoverTap } from "../animations/animations";

export default function PomodoroSingle() {
  const { id } = useParams();
  const { setIsLoading, setEditMode, editMode, fakeId } = useGlobalContext();
  const { user } = useAuthContext();
  const {
    focusTimeSeconds,
    setFocusTimeSeconds,
    setRestTimeSeconds,

    isRest,
    setIsRest,
    getAllPomodoro,
    singlePomodoro,
    getSinglePomodoro,
  } = useTaskContext();

  const [focusTime, setFocusTime] = useState(
    new Date().setSeconds(new Date().getSeconds() + focusTimeSeconds)
  );

  const titleEdit = useRef();
  const navigate = useNavigate();

  useEffect(() => {
    setIsLoading(true);
    getSinglePomodoro(id);
    fakeId && navigate("/pomodoro");
    user || getAllPomodoro();
    titleEdit.current = singlePomodoro?.title;
    return setIsLoading(false);
  }, [fakeId]);

  return (
    <m.div
      variants={pageAnimationLeft}
      initial="hidden"
      animate="show"
      exit="exit"
      className={isRest ? "pomo-single rest" : "pomo-single"}
    >
      <div className="nav-2">
        <m.div
          variants={buttonHoverTap}
          whileHover="hover"
          whileTap="tap"
          className="nav-2__close"
          onClick={() => {
            navigate("/pomodoro");
          }}
        >
          CLOSE
        </m.div>
      </div>

      <div className="pomo-single__title-container">
        <div
          className={
            singlePomodoro?.favorite
              ? "pomo-single__title fave"
              : "pomo-single__title"
          }
        >
          {singlePomodoro?.title}
        </div>
      </div>

      <div className="pomo-single__value">TOTAL: {singlePomodoro?.value}</div>
      <div className="subheader__container">
        <m.button
          variants={buttonHoverTap}
          whileHover="hover"
          whileTap="tap"
          className="pomo edit-button"
          onClick={() => {
            setEditMode(true);
            titleEdit.current = singlePomodoro.title;
          }}
        >
          EDIT
        </m.button>

        <div className=" pomo form-date">
          {new Date(singlePomodoro?.updatedAt).toDateString()}
        </div>
      </div>
      <div className="body__container">
        <PomodoroTimer
          expiryTimestamp={focusTime}
          key={focusTime}
        ></PomodoroTimer>
        <div className="all-timer__container">
          <label className="timer__time__container">
            <div className="label-time">FOCUS TIME:</div>
            <br />
            <select
              className="options__container"
              id="status"
              onChange={(e) => {
                setIsRest(null);
                let time = parseInt(e.target.value);
                setFocusTimeSeconds(time);
                setFocusTime(
                  new Date().setSeconds(new Date().getSeconds() + time)
                );
              }}
            >
              <option value="1200" className="options__item">
                20 MINS
              </option>
              <option value="600" className="options__item">
                10 MINS
              </option>
              <option value="300" className="options__item">
                5 MINS
              </option>
              <option value="60" className="options__item">
                1 MIN
              </option>
            </select>
          </label>
          <label className="timer__time__container">
            <div className="label-time">REST TIME:</div>
            <br />
            <select
              className="options__container"
              id="status"
              onChange={(e) => {
                setIsRest(null);

                let time = parseInt(e.target.value);
                setRestTimeSeconds(time);
              }}
            >
              <option value="60" className="options__item">
                1 MIN
              </option>
              <option value="300" className="options__item">
                5 MINS
              </option>

              <option value="600" className="options__item">
                10 MINS
              </option>

              <option value="900" className="options__item">
                15 MINS
              </option>
            </select>
          </label>
        </div>
      </div>
      <AnimatePresence>
        {editMode && <PomodoroEdit></PomodoroEdit>}
      </AnimatePresence>
    </m.div>
  );
}
